<template>
  <div>
    <b-form-group>
      <label for="title">Title</label>
      <b-form-select
      :options="options"
      id="title"
      required
      v-model="data.title"></b-form-select>
    </b-form-group>

    <b-form-group>
      <label for="firstName">First Name</label>
      <b-form-input
      type="text"
      id="firstName"
      required
      v-model="data.firstName"></b-form-input>
    </b-form-group>

    <b-form-group>
      <label for="lastName">Last Name</label>
      <b-form-input
      type="text"
      id="lastName"
      required
      v-model="data.lastName"></b-form-input>
    </b-form-group>

    <b-form-group>
      <label for="url">URL</label>
      <b-form-input
      type="url"
      id="url"
      required
      v-model="data.url"></b-form-input>
    </b-form-group>
  </div>
</template>

<script>
/* eslint-disable */
import Vue from 'vue';
import { FormSelectPlugin, FormInputPlugin, FormGroupPlugin, } from 'bootstrap-vue';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-vue/dist/bootstrap-vue.css';

Vue.use(FormInputPlugin);
Vue.use(FormGroupPlugin);
Vue.use(FormSelectPlugin);

const options = [
  { value: null, text: 'N/A' },
  { value: 'Mr', text: 'Mr' },
  { value: 'Mrs', text: 'Mrs' },
  { value: 'Ms', text: 'Ms' },
  { value: 'Dr', text: 'Dr' }
];

export default {
  name: 'AdviserAdviseeForm',
  props:['data'],
  data() {
    return {
      options: options
    }
  },
}
</script>



